/* eslint-disable react/prop-types */
import React from "react";

function DataIteration(props) {
  const { datas = [], startLength, endLength, children } = props;
  return (
    <>
      {datas &&
        datas.length >= endLength &&
        datas
          .slice(startLength, endLength)
          .map((value, index) => {
            // Compatibilit√© Safari - remplacer optional chaining
            const key = (value && value.id) || (value && value.slug) || `data-iteration-${startLength + index}`;
            return (
              <React.Fragment key={key}>
                {children({ datas: value })}
              </React.Fragment>
            );
          })}
    </>
  );
}

export default DataIteration;
